.nav-bar {
  background-color: #181818;
  width: 60px;
  height: 100%;
  position: absolute;
  top: 0;
  z-index: 3;
  min-height: 500px;
}
.nav-bar .logo {
  display: block;
  padding: 8px 0;
}
.nav-bar .logo img {
  display: block;
  margin: 8px auto;
  width: 24px;
  height: auto;
}
.nav-bar .logo img.sub-logo {
  width: 50px;
}
.nav-bar nav {
  display: block;
  text-align: center;
  position: absolute;
  height: 210px;
  top: 50%;
  margin-top: -120px;
  width: 100%;
}
.nav-bar nav a {
  font-size: 22px;
  color: #4d4d4e;
  display: block;
  line-height: 51px;
  height: 51px;
  position: relative;
  text-decoration: none;
}
.nav-bar nav a i {
  transition: all 0.3s ease-out;
}
.nav-bar nav a:hover {
  color: white;
}
.nav-bar nav a:hover svg {
  opacity: 0;
}
.nav-bar nav a:hover:after {
  opacity: 1;
}
.nav-bar nav a:after {
  content: "";
  font-size: 9px;
  letter-spacing: 2px;
  position: absolute;
  bottom: 0;
  display: block;
  width: 100%;
  text-align: center;
  opacity: 0;
  transition: all 0.3 ease-out;
}
.nav-bar nav a:first-child::after {
  content: "HOME";
}
.nav-bar nav a.about-link:after {
  content: "ABOUT";
}
.nav-bar nav a.portfolio-link:after {
  content: "MY WORK";
}
.nav-bar nav a.contact-link:after {
  content: "CONTACT";
}
.nav-bar nav a.active svg {
  color: white;
}
.nav-bar ul {
  position: absolute;
  bottom: 20px;
  width: 100%;
  display: block;
  padding: 0;
  list-style: none;
  text-align: center;
  margin: 0;
}
.nav-bar ul li a {
  padding: 7px 0;
  display: block;
  font-size: 15px;
  line-height: 16px;
  color: #4d4d4e;
}
.nav-bar ul li:hover svg {
  color: white;
}

.hamburger-icon, .close-icon {
display: none;
}


@media screen and (max-width: 900px) {
.nav-bar {
  background: transparent;
  position: initial;
  height: auto;
  min-height: auto;
}
  .nav-bar ul, .nav-bar nav {
    display: none;
  }

  .nav-bar nav {
    width: 100%;
    height: 100%;
    background: #181818;
    top: 0;
    left: 0;
    position: fixed;
    z-index: 2;
    margin: 0;
  }

  .nav-bar nav.mobile-show {
    display: block;
  }

  .nav-bar nav a {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .nav-bar nav a svg {
    opacity: 1 !important;
  }

  .nav-bar nav a:after {
    opacity: 1;
    position: initial;
    width: auto;
    margin-left: 10px;
  }

  .nav-bar .hamburger-icon, .nav-bar .close-icon {
    display: block;
    position: absolute;
    top: 15px;
    right: 15px;
    z-index: 1;
  }
}