.page {
    width: 100%;
    height: 100%;
    position: absolute;
  }
  
  .top-tags {
    bottom: auto;
    top: 35px;
  }
  
  .tags {
    color: yellow;
    opacity: 0.6;
    position: absolute;
    bottom: 0;
    left: 120px;
    font-size: 18px;
    font-family: "La Belle Aurore";
  }
  
  .bottom-tag-html {
    margin-left: -20px;
  }
  
  .container {
    width: 100%;
    will-change: contents;
    height: 90%;
    min-height: 566px;
    position: absolute;
    opacity: 0;
    top: 5%;
    margin: 0 auto;
    z-index: 1;
    transform-style: preserve-3d;
    animation: fadeIn 1s forwards;
    animation-delay: 1s;
  }
  

  .container.contact-page h1,
  .container.portfolio-page h1,
  .container.about-page h1 {
    font-size: 53px;
    font-family: "Coolvetica";
    color: white;
    font-weight: 400;
    margin-top: 0;
    position: relative;
    margin-bottom: 40px;
    left: -10px;
  }
  .container.contact-page h1:before,
  .container.portfolio-page h1:before,
  .container.about-page h1:before {
    content: "<h1>";
    font-family: "La Belle Aurore", cursive;
    color: yellow;
    font-size: 18px;
    position: absolute;
    margin-top: -10px;
    left: -10px;
    opacity: 0.6;
    line-height: 18px;
  }
  .container.contact-page h1:after,
  .container.portfolio-page h1:after,
  .container.about-page h1:after {
    content: "</h1>";
    font-family: "La Belle Aurore", cursive;
    color: yellow;
    font-size: 18px;
    line-height: 18px;
    position: absolute;
    left: -30px;
    bottom: -20px;
    margin-left: 20px;
    opacity: 0.6;
  }
  .container.contact-page p,
  .container.portfolio-page p,
  .container.about-page p {
    font-size: 13px;
    color: #fff;
    font-family: sans-serif;
    font-weight: 300;
    max-width: fit-content;
    animation: pulse 1s;
  }
  .container.contact-page p:nth-of-type(1),
  .container.portfolio-page p:nth-of-type(1),
  .container.about-page p:nth-of-type(1) {
    animation-delay: 1.1s;
  }
  .container.portoflio-page p:nth-of-type(2),
  .container.about-page p:nth-of-type(2) {
    animation-delay: 1.2s;
  }
  .container.about-page p:nth-of-type(3) {
    animation-delay: 1.3s;
  }
  .container.contact-page .text-zone,
  .container.portfolio-page .text-zone,
  .container.about-page .text-zone {
    position: absolute;
    left: 10%;
    top: 50%;
    transform: translateY(-50%);
    width: 35%;
    vertical-align: middle;
    display: table-cell;
    max-height: 90%;
  }
  .container.contact-page .text-animate-hover:hover,
  .container.portfolio-page .text-animate-hover:hover,
  .container.about-page .text-animate-hover:hover {
    color: yellow;
  }

  @media screen and (max-width: 900px) {
    .page {
      position: initial;
    }

    .container {
      position: initial;
      height: auto;
      min-height: auto;
    }

    .container.contact-page .text-zone,
    .container.about-page .text-zone,
    .container.portfolio-page .text-zone {
      position: initial;
      transform: none;
      width: 100%;
      display: block;
      padding: 20px;
      box-sizing: border-box;
    }
  }